.cta {
  background-color: var(--color-bg-secondary);
  padding: 80px 32px 80px;
  text-align: center;
  margin-bottom: 40px;
}
@media (max-width: 768px) {
  .cta {
    padding: 64px 20px;
  }
}
.cta-wrapper {
  max-width: 900px;
  margin: 0 auto;
}
.cta__title {
  margin-bottom: 15px;
}
@media (max-width: 768px) {
  .cta__title {
    max-width: 440px;
    margin: 0 auto;
    margin-bottom: 18px;
  }
}
@media (max-width: 480px) {
  .cta__title {
    max-width: 250px;
  }
}
.cta__title-decor {
  position: relative;
}
.cta__title-decor::after {
