    display: block;
    max-width: 343px;
    margin: 0 auto;
  }
}

.page-footer {
  position: relative;
}
.page-footer__wrapper {
  max-width: 1920px;
  padding: 0 32px;
  margin: 0 auto;
}
@media (max-width: 768px) {
  .page-footer__wrapper {
    padding: 0 20px;
  }
}
.page-footer::before {
  content: "";
  position: absolute;
  height: 1px;
  background-color: var(--color-border);
  top: 0;
  left: 0;
  right: 0;
}

.footer-top {
  padding: 40px 0 42px;
  display: grid;
  grid-template-areas: "socials menu" "logo menu";
  grid-template-columns: 1fr 3fr;
}
@media (max-width: 1024px) {
  .footer-top {
    grid-template-areas: "logo socials" "menu menu";
    row-gap: 40px;
    padding-bottom: 10px;
  }
}
.footer-top__logo-link {
  justify-self: start;
}
.footer-top__socials-list {
  grid-area: socials;
  display: flex;
  flex-direction: column;
  gap: 15px;
  margin-bottom: 30px;
}
@media (max-width: 1024px) {
  .footer-top__socials-list {
    flex-direction: row;
    justify-content: flex-end;
    align-items: center;
    margin-bottom: 0;
    gap: 24px;
  }
}
.footer-top__socials-item {
  display: flex;
}
.footer-top__socials-link {
  display: inline-block;
  padding-left: 24px;
  background-repeat: no-repeat;
  background-position: left center;
  background-size: 18px 18px;
  transition: transform 0.2s linear 0s;
}
.footer-top__socials-link:link, .footer-top__socials-link:visited {
  color: var(--color-text-secondary);
}
.footer-top__socials-link:hover {
