.design-content {
    width: 100%;
    max-width: 1920px;
    margin: 0 auto;
    padding: 0 31px 0 32px;

    &__inner > * {
        min-width: 0;
    }

    &__inner {
        width: 100%;
        display: grid;
        grid-template-areas:
            "filters search "
            "filters tags"
            "filters breadcrumbs"
            "filters templates"
            "filters pagination";

        grid-template-columns: 213px 1fr;
        column-gap: 40px;

        @media (max-width: 1024px) {
            grid-template-areas:
                "filters search "
                "tags tags"
                "breadcrumbs breadcrumbs"
                "templates templates"
                "pagination pagination";

            grid-template-columns: 161px 1fr;
            column-gap: 20px;
        }

        @media (max-width: 768px) {
            grid-template-areas:
                "search "
                "tags"
                "breadcrumbs"
                "filters"
                "templates"
                "pagination";

            grid-template-columns: auto;
        }
    }
    &__inner--filters-closed {
        grid-template-areas:
            "filters search "
            "tags tags"
            "breadcrumbs breadcrumbs"
            "templates templates"
            "pagination pagination";
    }
}

// 
.design-search {
    grid-area: search;
    margin-bottom: 20px;
    position: relative;
}

.design-search__input {
    font-size: 14px;
    line-height: 18.2px;
    border: 1px solid var(--color-border);
    border-radius: 4px;
    padding: 10px 50px 12px;
    width: 100%;
}

.design-search__btn {
    position: absolute;
    top: 50%;
    left: 20px;
    transform: translateY(-50%);
    width: 20px;
    height: 20px;
    border: none;
    background-color: transparent;
    background-image: url(../img/search.svg);
    background-repeat: no-repeat;
    background-size: 20px 20px;
    background-position: center;
    cursor: pointer;
}

// 
.design-tags {
    grid-area: tags;
    position: relative;
    margin-bottom: 30px;
    z-index: 1;

    @media (max-width: 768px) {
        margin-bottom: 24px;
    }
}

.design-tags__list {
    display: flex;
    gap: 10px;
    overflow-x: auto;
}

.design-tags__list::-webkit-scrollbar {
    display: none;
}

.design-tags__item {
    flex-shrink: 0;
}

.design-tags__tag {
    font-size: 14px;
    padding: 7px 20px;
    border: none;
    border-radius: 36px;
    background-color: var(--color-bg-first);
    cursor: pointer;
    transition: color 0.1s linear 0s;

    &:hover {
        color: var(--color-secondary);
    }
}

.design-tags__arrow-btn {
    position: absolute;
    transform: translateY(-50%);

    &--prev {
        left: -10px;
    }

    &--next {
        right: -10px;
    }
}

// BREADCRUMBS
.design-breadcrumbs {
    grid-area: breadcrumbs;
    padding: 0;
    display: flex;
    justify-content: space-between;
    width: 100%;
    margin-bottom: 30px;
    z-index: 1;

    @media (max-width: 768px) {
        margin-bottom: 24px;
    }
}

.breadcrumbs__templates {
    font-size: 14px;

    @media (max-width: 480px) {
        display: none;
    }
}

// 
.design-templates {
    grid-area: templates;
    display: grid;
    grid-template-columns: repeat(auto-fit, 212px);
    grid-template-rows: repeat(4, 243px);
    column-gap: 20px;
    row-gap: 30px;
    text-align: center;
    justify-content: center;
    margin-bottom: 40px;

    @media (max-width: 768px) {
        margin-bottom: 32px;
    }
}

.design-template {
    text-align: left;
    position: relative;
}

.design-template:hover .design-template__favourite {
    display: flex;
}

.design-template__favourite {
    position: absolute;
    top: 10px;
    right: 10px;
    width: 28px;
    height: 28px;
    border: none;
    border-radius: 4px;
    background-color: rgba(31, 33, 40, 0.4);
    justify-content: center;
    align-items: center;
    display: none;
    cursor: pointer;
}

.design-template__blank-img {
    width: 212px;
    height: 216px;
    background-color: var(--color-bg-first);
    background-image: url(../img/crest.svg);
    background-repeat: no-repeat;
    background-size: 24px 24px;
    background-position: center;
    border-radius: 4px;
    margin-bottom: 10px;
    transition: all 0.2s linear 0s;

    &:hover {
        box-shadow: 0px 8px 25px 0px rgba(40, 41, 54, 0.2);
    }
}

.design-template__img {
    border-radius: 4px;
    margin-bottom: 10px;
    transition: all 0.2s linear 0s;
}

.design-template__title {
    font-size: 14px;
    font-weight: 500;
    line-height: normal;
    color: var(--color-text-main);
    display: -webkit-box;
    -webkit-line-clamp: 1;
    -webkit-box-orient: vertical;
    overflow: hidden;
    text-overflow: ellipsis;
    transition: all 0.2s linear 0s;
}

.design-template:hover .design-template__img {
    box-shadow: 0px 6px 10px 3px rgba(40, 41, 54, 0.15);
}

.design-template:hover .design-template__title {
    font-weight: 700;
}

// 
.design-pagination {
    grid-area: pagination;
    margin-bottom: 80px;

    @media (max-width: 768px) {
        margin-bottom: 48px;
    }
}

.design-pagination__list {
    display: flex;
    justify-content: center;
    align-items: center;
}

.design-pagination__link {
    font-size: 14px;
    color: var(--color-text-main);
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 4px;
    width: 32px;
    height: 32px;

    &:hover {
        background-color: var(--color-bg-first);
    }
}

.design-pagination__prev,
.design-pagination__next {
    display: flex;
    align-items: center;
    background-color: transparent;
    border: none;
    cursor: pointer;

    &:hover {
        transform: scale(1.3);
    }
}

.design-pagination__prev {
    margin-right: 10px;
}

.design-pagination__next {
    margin-left: 10px;
}
